import React from "react";
import wrapper from "../../../commons/WebComponentWrapper";
import FormLabel from "@mui/material/FormLabel";

const dispatchedEvents = [];

const SrsMuiFormLabel = function (props, ref) {
  return (
    <FormLabel
      error={props.error}
      disabled={props.disabled}
      required={props.required}
      focused={props.focused}
      filled={props.filled}
      variant={props.variant}
      htmlFor={props.htmlFor}
      theme={props.muiTheme}
    >
      <slot />
    </FormLabel>
  );
};

const styles = `
  /* Custom styles for the MuiFormLabel component */
`;

const props = [
  {
    name: "error",
    defaultValue: false,
    type: "boolean",
  },
  {
    name: "disabled",
    defaultValue: false,
    type: "boolean",
  },
  {
    name: "required",
    defaultValue: false,
    type: "boolean",
  },
  {
    name: "focused",
    defaultValue: false,
    type: "boolean",
  },
  {
    name: "filled",
    defaultValue: false,
    type: "boolean",
  },
  {
    name: "variant",
    defaultValue: "standard",
    type: "string",
  },
  {
    name: "htmlFor",
    defaultValue: "",
    type: "string",
  },
  {
    name: "sx",
    defaultValue: {},
    type: "object"
  }
  // Add other props specific to FormLabel if needed
];

const slots = [
  {
    name: "default",
    allowed: [],
  },
];

const WrappedComponent = wrapper(
  React.forwardRef(SrsMuiFormLabel),
  props,
  "mui"
);
export { WrappedComponent, styles, props, dispatchedEvents, slots };
